.login{
    background-image: url('/assets/loginback.jpeg');
    background-size:cover;
    width: 100%;
    height: 100%;
    min-height: 94vh;
}


.contLogin{
    border-radius: 20px;
    background:rgb(220, 220, 220); 
    min-height: 74vh;
}

.userImg{
    
    width: 200px;
     height: 180px;
     border-radius: 50%;
     
}

.userImg img{
    width: 100%;
}

.logoFooter img{
    width: 55px;
    margin-bottom: 1%;
}

.GenFooter{
    width: 100%;
    height: 100%;
}

@media screen and (max-width:453px) {
    .logoFooter img{
        width: 50px;
        margin-bottom: 5%;
    }
}
.lateralMenu{
    background:rgb(1, 35, 57);
    font-size: 23px;
}

.lateralMenu h4{
    margin-left: 5%;
}
.lateralMenu ul{
    margin-left: 5%;
}

.dropDownMenu{
    background:rgb(2, 46, 74);
}
.dropDownMenu li a:hover{
    background:rgb(4, 69, 110);
}


.navbar-brand img{
    width: 180px;
    margin-left: 5%;
}

@media screen and (max-width:453px) {
    .navbar-brand img{
        width: 139.1px;
        margin-left: 5%;
    }
}

.filling{
    height: 150px;
    
}
@media screen and (max-width:614px) {
    .dissapearObject{
        display: none;
    }
}

@media screen and (min-width:614px) {
    .appearMovileObject{
        display: none;
    }

}

.especialButtonClose{
    background: none;
    border-color: none;
    border: none;
    font-size: 35px;
}

.especialButtonClose {
    margin-bottom: 10px;
}

.generalContainer{
    height: 100vh;
    margin-bottom: 150px;
}

.tbody {
    display: block;
    height: 50vh;
    overflow: auto;
}


.thead,
.tfoot,
.tbody tr {
    width: calc(100% - 1em);
    display: table;
}

.thead,
.tfoot {
    width: calc(100% - 1em);
    font-weight: bold
}

.row_hover:hover {
    background-color: darkgray;
}
